// ----- Button
.btn {
    font-size:14px;
    line-height:20px;
    font-weight:600;
    padding:10px 20px;
    border:none;
    border-radius:4px;
    cursor: pointer;
    color:$black-clr;
    background-color:$grey-clr;
    outline:none;
    box-shadow:none;
    text-shadow:none;
    // Max Width 767px
    @media only screen and (max-width: $break_767) {
        padding:8px 20px;
    }
    &:focus, &:active, &:hover {
        color:$black-clr;
        background-color:$grey3-clr;
        border:none;
        outline:none !important;
        box-shadow:none !important;
        text-shadow:none;
    }
    /* Btn Main */
    &.btn-main {
        color:$white-clr;
        background-color:$primary-clr;
        &:hover, &:focus, &:active {
            color:$white-clr;
            background-color:darken($primary-clr, 5%);
        }
    }
    /* Btn Dark */
    &.btn-dark {
        color:$white-clr;
        background-color:lighten($black-light-clr, 10%);
        &:hover, &:focus, &:active {
            color:$white-clr;
            background-color:$black-light-clr;
        }
    }
    /* Btn Grey */
    &.btn-grey {
        color:$white-clr;
        background-color:$grey2-clr;
        &:hover, &:focus, &:active {
            color:$white-clr;
            background-color:darken($grey2-clr, 5%);;
        }
    }
    /* Btn Red */
    &.btn-red {
        color:$white-clr;
        background-color:$secondary-clr;
        &:hover, &:focus, &:active {
            color:$white-clr;
            background-color:darken($secondary-clr, 5%);;
        }
    }
}
