// ----- Header
#chef-dashboard-header {
    background-color:$black-light-clr;
    padding:10px 0;
    .sidebar-tog {
        a {
            display: inline-block;
            img {
                height:30px;
                // Max Width 767px
                @media only screen and (max-width: $break_767) {
                    height:24px;
                }
            }
        }
    }
    .logo-sec {
        text-align: center;
        a {
            display: inline-block;
            img {
                height:50px;
                // Max Width 767px
                @media only screen and (max-width: $break_767) {
                    height:40px;
                }
            }
        }
    }
    .back-home {
        text-align: right;
        a {
            color:$white-clr;
            text-transform: capitalize;
            &:hover {
                color:$primary-clr;
            }
            // Max Width 767px
            @media only screen and (max-width: $break_767) {
                font-size:12px;
            }
        }
    }
}

// ----- Sidebar
#chef-dashboard-sidebar {
    background-color:$black-light-clr;
    max-width:260px;
}