// ----- Reset
body {
    font-family:$primary-ff;
    font-size: $base-fz;
    line-height:1.5;
    font-weight:400;
    color:$black-light-clr;
    background-color:$white-clr;
    overflow-x: hidden;
    &.sidebar1-active {
        overflow: hidden;
        &:after {
            
        }
    }
}
ul,ol {
    list-style-type:none;
    padding-left:0px;
    margin-bottom:0px;
}
h1, h2, h3, h4, h5, h6 {
    font-weight:500;
    margin-bottom:15px;
    line-height:1.2;
}
a {
    text-decoration: none;
    &:hover {
        text-decoration: none;
    }
}